import React from 'react';
import { Route, Routes } from 'react-router-dom';
import Header from '../Header/Header'
import Aside from '../Aside/Aside'
import Home from './Home/Home';
import MyAssets from './MyAssets/MyAssets';
import MyAssetsItems from './MyAssets/Assets/AssetsItem';
import Connections from './Connections/Connections';
import SocialPublish from './SocialPublish/SocialPublish';
import Billing from './Billing/Billing';
import Profile from './Profile/Profile';
import EditProfile from './Profile/EditProfile';
import Products from './Shoppable/Products';
import TagProducts from './Shoppable/TagProducts';
import Trash from './Trash/Trash';
import WebEmbed from './WebEmbed/WebEmbed';
import DigitalScreen from './DigitalScreen/DigitalScreen';
import SocialAds from './SocialAds/SocialAds';
import PrintMedia from './PrintMedia/PrintMedia';
import EmailCampaign from './EmailCampaign/EmailCampaign';
import HashtagCampaign from './HashtagCampaign/HashtagCampaign';
import Templates from './Templates/Templates';
import Template from './Templates/Components/Template';
import Rule from './Rule/Rule';
import Analytics from './Analytics/Analytics';

const Wrapper = ({ children }) => {
    var pageName = window.location.pathname;
    pageName = pageName.split("/");
    //console.log('pageName', pageName)
    const SubHeaderPages = ["home", "trash"];
    const ActiveSubheader =  SubHeaderPages.includes(pageName[1]);
    
    

    return  <div className={`app-page d-flex flex-column flex-column-fluid flex-column align-items-stretch h-100${!ActiveSubheader ? ' has-subheader' : ''}`}>
                <Header />
                <Aside />
                <main className="main_content position-relative">
                    {children}
                </main>
            </div>
}


function MainCompomentRouter() {
    return <Routes>
            <Route exact path="/" element={<Wrapper><Home /></Wrapper>}></Route>
            <Route exact path="/home/" element={<Wrapper><Home /></Wrapper>}></Route>
            <Route path="/assets/" element={<Wrapper><MyAssets /></Wrapper>}></Route>
            <Route path="/assets/:items" element={<Wrapper><MyAssetsItems /></Wrapper>}></Route>
            <Route path="/connections" element={<Wrapper><Connections /></Wrapper>}></Route>
            <Route path="/billing/" element={<Wrapper><Billing /></Wrapper>}></Route>
            <Route path="/profile/" element={<Wrapper><Profile /></Wrapper>}></Route>
            <Route path="/profile/edit" element={<Wrapper><EditProfile /></Wrapper>}></Route>
            <Route path="/products/" element={<Wrapper><Products /></Wrapper>}></Route>
            <Route path="/products/tag" element={<Wrapper><TagProducts /></Wrapper>}></Route>
            <Route path="/trash" element={<Wrapper><Trash /></Wrapper>}></Route>
            <Route path="/web-embed/" element={<Wrapper><WebEmbed /></Wrapper>}></Route>
            <Route path="/digital-screen/" element={<Wrapper><DigitalScreen /></Wrapper>}></Route>
            <Route path="/social-ads/" element={<Wrapper><SocialAds /></Wrapper>}></Route>
            <Route path="/email-campaign/" element={<Wrapper><EmailCampaign /></Wrapper>}></Route>
            <Route path="/print-media/" element={<Wrapper><PrintMedia /></Wrapper>}></Route>
            <Route path="/hashtag-campaign/" element={<Wrapper><HashtagCampaign /></Wrapper>}></Route>
            <Route path="/template/" element={<Wrapper><Templates /></Wrapper>}></Route>
            <Route path="/template/add" element={<Wrapper><Template /></Wrapper>}></Route>
            <Route path="/template/edit" element={<Wrapper><Template /></Wrapper>}></Route>
            <Route path="/social-publish/" element={<Wrapper><SocialPublish /></Wrapper>}></Route>
            <Route path="/rule/" element={<Wrapper><Rule /></Wrapper>}></Route>
            <Route path="/analytics/" element={<Wrapper><Analytics /></Wrapper>}></Route>
        </Routes>
}

export default MainCompomentRouter;